import axios from 'axios'
import baseUrl from '@/api/base'

const logicGroupList = {
  // 获取部署记录
  getLogicGroup: params => {
    return axios({
      method: 'get',
      url: `${baseUrl}/abilityDeploySoftware/getLogicGroup`,
      params: params
    }).then(res => {
      return res
    }).catch(err => {
      console.log('请求失败: ', err)
      return err
    })
  }
}

export default logicGroupList



