<style lang="scss" scoped>
  @import "../../assets/scss/List";
</style>

<template>
  <section class='box'>
    <!--搜索条-->
    <el-col :span="24" class="search">
      <el-form :inline="true" :model="filters">
        <el-form-item  >
          <span class="search-label">逻辑组：</span>
          <el-input v-model="filters.logicName"></el-input>
        </el-form-item>
        <el-form-item >
          <span class="search-label">集群：</span>
          <el-input v-model="filters.colony"></el-input>
        </el-form-item>
        <el-form-item >
          <el-button type="primary" @click="getLogicData" >查询</el-button>
        </el-form-item>
        <el-form-item  >
          <el-button type="primary">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--列表内容-->
    <el-table :data="logicData" stripe border v-loading="loading" element-loading-text='加载中...'>
      <el-table-column prop="logicName" label="逻辑组" sortable>
      </el-table-column>
      <el-table-column prop="VMPcolony" label="VMP集群" sortable>
      </el-table-column>
      <el-table-column label='操作'>
        <template slot-scope='scope'>
          <el-button type='danger' size='small' @click='removeAssociationRecord(scope.$index, scope.row)'>删除</el-button>
          <el-button size='small' @click='handleDetails(scope.$index, scope.row)'>查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--分页控件-->
    <el-col :span='24' class="pagination">
      <el-pagination
        @size-change='handleSizeChange'
        @current-change='handleCurrentChange'
        :current-page='pagination.currentPage'
        :page-sizes='[10, 20, 30, 40]'
        :page-size='pagination.limit'
        :total='pagination.total'
        layout='total, sizes, prev, pager, next, jumper'
        style='float:right;'>
      </el-pagination>
    </el-col>
  </section>
</template>

<script>
  // 将每个页面的所有api都放在一个api文件中去管理，@为 src 目录
  import availabilityAssociation from '@/api/availabilityAssociation'
  import moment from 'moment'

  export default {
    data () {
      return {
        loading: false,
        logicData: [],
        filters: {
          logicName: '',
          colony: ''
        },
        // 分页控件
        pagination: {
          limit: 10, // 分页每页多少条
          start: 0,
          currentPage: 1,
          total: 0
        }
      }
    },
    mounted () {
      // 页面挂载结束请求用户列表
      this.getLogicData()
    },
    methods: {
      // 分页条目
      handleSizeChange: function (val) {
        this.pagination.limit = val
        this.getLogicData(this.pagination.start, this.pagination.limit)
      },
      // 分页切换页面
      handleCurrentChange: function (val) {
        this.pagination.start = (val - 1) * this.pagination.limit
        this.getLogicData(this.pagination.start, this.pagination.limit)
      },
      // 获取以及搜索用户
      getLogicData: function () {
        var self = this
        this.loading = true
        let params = {
          limit: this.pagination.limit,
          start: this.pagination.start,
          logicName: this.filters.logicName,
          colony: this.filters.colony
        }

        availabilityAssociation.getLogicData(params).then((res) => {
          this.loading = false

          if (res.data.success === true) {
            this.pagination.total = res.data.value.totalCount
            this.logicData = res.data.value.rows
          } else {
            this.$message({
              message: res.data.msg,
              type: 'error'
            })
          }
        })
      },
      // 删除单个用户
      removeAssociationRecord: function (index, row) {
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          let id = row.id

          availabilityAssociation.removeAssociationRecord(id).then((res) => {
            this.listLoading = false

            if (res.data.success === true) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              })
              this.getLogicData()
            } else {
              this.$message({
                message: res.data.msg,
                type: 'error'
              })
            }
          })
        })
      },
      // 查看记录
      handleDetails: function (index, row) {
        this.editFormVisible = true
        this.editForm = Object.assign({}, row)
      }
    }
  }
</script>


