import NotFound from '@/views/404'
import Layout from '@/components/Layout'
// 边缘计算页面
import AvailabilityAssociation from '@/views/ec/AvailabilityAssociation'
import AvailabilityAssociationAdd from '@/views/ec/AvailabilityAssociationAdd'
import ApplicationDeploy from '@/views/ec/ApplicationDeploy'
import AbilityDeploy from '@/views/ec/AbilityDeploy'
import AbilityDeploySoftware from '@/views/ec/AbilityDeploySoftware'
import ServiceState from '@/views/ec/ServiceState'

// 配置路由
const routes = [
  { path: '/404', component: NotFound, name: '404', hidden: true },
  { path: '/', redirect: '/ec/availability-association', hidden: true },
  {
    path: '/ec', component: Layout, redirect: '/ec/availability-association', name: '边缘计算',
    children: [
      { path: 'availability-association', component: AvailabilityAssociation, name: '集群配对' },
      { path: 'availability-association/add', component: AvailabilityAssociationAdd, name: '集群配对新增' },
      { path: 'application-deploy', component: ApplicationDeploy, name: '应用配置' },
      { path: 'ability-deploy', component: AbilityDeploy, name: '能力部署' },
      { path: 'ability-deploy/software', component: AbilityDeploySoftware, name: '能力部署（软件包名）' },
      { path: 'service-state', component: ServiceState, name: '服务运行状态' }
    ]
  },
  { path: '*', hidden: true, redirect: { path: '/404' } }
]

export default routes



